
import { ref, defineComponent } from "vue";
import { getColorMode } from "../global";
import tinycolor from "tinycolor2";

export default defineComponent({
  name: "Gradient",
  components: {},
  props: {
    colorOne: {
      type: String,
      required: true,
    },
    colorTwo: {
      type: String,
      required: true,
    },
    colorThree: {
      type: String,
    },
    name: {
      type: String,
      required: true,
    },
  },
  setup(props) {
    const count = ref(0);
    const mode = getColorMode;
    const isShown = ref(false);
    const colorConv = ref("");

    const linearGradient = ref(
      props.colorThree == "none"
        ? "linear-gradient(to right, " +
            props.colorOne +
            ", " +
            props.colorTwo +
            ")"
        : "linear-gradient(to right, " +
            props.colorOne +
            ", " +
            props.colorTwo +
            ", " +
            props.colorThree +
            ")"
    );

    function hideTooltip() {
      isShown.value = false;
    }
    function copyColor(color: string) {
      switch (mode.value) {
        case "hex":
          colorConv.value = tinycolor(color).toHexString();
          break;
        case "rgb":
          colorConv.value = tinycolor(color).toRgbString();
          break;
        case "hsv":
          colorConv.value = tinycolor(color).toHsvString();
          break;
        case "hsl":
          colorConv.value = tinycolor(color).toHslString();
          break;
      }
      navigator.clipboard.writeText(colorConv.value).then(
        function () {
          console.log("success");
          isShown.value = true;
          setTimeout(hideTooltip, 1000);
        },
        function () {
          console.log("error");
        }
      );
    }

    return {
      count,
      copyColor,
      linearGradient,
      mode,
      isShown,
      colorConv,
    };
  },
});
